@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700);
body {
  margin: 0;
  font-family: Poppins, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --main-background: #172a45;
  --secondary: #8892b0;
  --white: ghostwhite;
  --light-secondary: rgb(136, 146, 176);
  --icon-main: rgb(23, 42, 69);
  --icon-secondary: #c0c0c0;
  --uiuc-orange: #e84a27;
  --uiuc-orange-secondary: rgba(232, 74, 39, 0.15);

  /* Company colors */
  --facebook: #39579c;
  --facebook-secondary: rgba(57, 87, 156, 0.15);
  --salesforce: #17a0db;
  --salesforce-secondary: rgba(23, 160, 219, 0.15);
  --tableau: #4e79a7;
  --tableau-secondary: rgba(78, 121, 167, 0.15);
  --motorola: black;
  --motorola-secondary: rgba(0, 0, 0, 0.15);
  --caterpillar: #d5b60a;
  --caterpillar-secondary: rgba(213, 182, 10, 0.15);
  --ameren: #35962b;
  --ameren-secondary: rgba(53, 150, 43, 0.15);
}

.about-root {
  text-align: center;
  background-color: #f2f2f2;
  padding-bottom: 3%;
}

img {
  padding-top: 2%;
  padding-bottom: 2%;
}

.profile-picture {
  border-radius: 50%;
  width: 25%;
  max-width: 230px;
  object-fit: scale-down;
}

.about-header {
  display: flex;
  justify-content: center;
  padding: 5% 15% 0% 15%;
}

.inner-text-container {
  padding-left: 4%;
}

.inner-text {
  text-align: left;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

@media (max-width: 630px) {
  .picture {
    width: 50%;
  }

  .about-header {
    display: inline;
    height: 100%;
    padding-left: 0%;
  }

  .inner-text {
    text-align: center;
    top: 0%;
    transform: translateY(-0%);
  }

  .inner-text-container {
    padding-left: 15%;
    padding-right: 15%;
  }
}

@media (max-width: 1000px) {
  .profile-picture {
    width: 40%;
  }
}

@media (max-width: 800px) {
  .profile-picture {
    width: 50%;
  }

  p {
    font-size: medium;
  }
}

.centered {
  text-align: center;
}

.about-text-container {
  text-align: left;
  padding-left: 15%;
  padding-right: 15%;
}

.flex-container {
  display: flex;
}

.boy-and-cat-drawing {
  min-width: 375px;
  width: 25%;
}

.home-root {
  height: 100%;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-title {
  color: var(--icon-main);
  width: 100%;
  text-align: center;
  font-size: 60vw;
  font-size: calc(12px + 1vw);
}

.social-icons {
  z-index: 1;
}

.social-icon-light {
  color: var(--white) !important;
}

.social-icon-light:hover {
  color: var(--light-secondary) !important;
}

.social-icon-dark {
  color: var(--icon-main) !important;
}

.social-icon-dark:hover {
  color: var(--icon-secondary) !important;
}

#github {
  padding-left: 20px;
  padding-right: 10px
}

#linkedin {
  padding-left: 10px;
  padding-right: 20px;
}

.projects-root {
  background-color: white;
  padding-bottom: 5%;
}

.card {
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 18rem;
  border-radius: 5px;
  color: black;
}

.card:hover {
  box-shadow: 0 1rem 2rem 0 rgba(0, 0, 0, 0.2);
}

.card-container {
  padding: 0.25rem 2rem;
}

.project {
  margin: 1% 2.5%;
}

.project-picture {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  width: 18rem;
  height: 18rem;
  object-fit: cover;
}

@media (max-width: 559px) {
  .project-picture {
    width: 13rem;
    height: 13rem;
  }
  .card {
    width: 13rem;
  }
}

.tech-icon-root {
  padding-left: 1%;
  padding-right: 1%;
  text-align: center;
}

.tech-title {
  padding-right: 2%;
}

.tech-label {
  margin: 0;
}

.skills-root {
  background-color: #f2f2f2;
  padding-bottom: 5%;
  padding-top: 5%;
}

.more-title {
  padding-bottom: 1%;
  text-align: center;
}

.more-root {
  padding-top: 4%;
  text-align: center;
}

.more-facts {
  text-align: center;
}

.cat-picture {
  border-radius: 50%;
  width: 15%;
}

@media (max-width: 800px) {
  .cat-picture {
    width: 50%;
  }
}

.counter-text {
  margin-top: 0;
}

.counter {
  padding: 0;
}

.footer-root {
  background-color: var(--icon-main);
  text-align: center;
  padding-bottom: 0.05%;
}

.credit {
  color: var(--light-secondary);
  margin-top: 0;
}

a {
  text-decoration: none;
  color: black;
}

.link {
  text-decoration: none;
}

#facebook {
  border-bottom: 2px solid var(--facebook);
  padding-left: 2px;
  padding-right: 2px;
}

#facebook:hover {
  background-color: var(--facebook-secondary);
  border-bottom: none;
  border-radius: 4px;
  color: var(--facebook);
}

#tableau {
  border-bottom: 2px solid var(--tableau);
  padding-left: 2px;
  padding-right: 2px;
}

#tableau:hover {
  background-color: var(--tableau-secondary);
  border-bottom: none;
  border-radius: 4px;
  color: var(--tableau);
}

#salesforce {
  border-bottom: 2px solid var(--salesforce);
  padding-left: 2px;
  padding-right: 2px;
}

#salesforce:hover {
  background-color: var(--salesforce-secondary);
  border-bottom: none;
  border-radius: 4px;
  color: var(--salesforce);
}

#motorola {
  border-bottom: 2px solid var(--motorola);
  padding-left: 2px;
  padding-right: 2px;
}

#motorola:hover {
  background-color: var(--motorola-secondary);
  border-bottom: none;
  border-radius: 4px;
  color: var(--motorola);
}

#caterpillar {
  border-bottom: 2px solid var(--caterpillar);
  padding-left: 2px;
  padding-right: 2px;
}

#caterpillar:hover {
  background-color: var(--caterpillar-secondary);
  border-bottom: none;
  border-radius: 4px;
  color: var(--caterpillar);
}

#ameren {
  border-bottom: 2px solid var(--ameren);
  padding-left: 2px;
  padding-right: 2px;
}

#ameren:hover {
  background-color: var(--ameren-secondary);
  border-bottom: none;
  border-radius: 4px;
  color: var(--ameren);
}

#uiuc-cs {
  border-bottom: 2px solid var(--uiuc-orange);
  padding-left: 2px;
  padding-right: 2px;
}

#uiuc-cs:hover {
  background-color: var(--uiuc-orange-secondary);
  border-bottom: none;
  border-radius: 4px;
  color: var(--uiuc-orange);
}

.navbar-root {
  left: 0;
  right: 0;
  top: 0;
  position: fixed;
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  z-index: 1;
}

.navbar-right-container {
  margin-left: auto;
}

.navbar-right-container > * {
  margin-left: 10px;
  cursor: pointer;
}

.navbar-right-container > *:first-child {
  margin-left: 0px;
}

hr {
  width: 70%;
}

.flex-container {
  display: flex;
  justify-content: center;
  padding-top: 1%;
  width: 100%;
  flex-wrap: wrap;
}

.App {
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
